
#winLoseDiv {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 0;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: lightgreen;
  position: fixed;
  visibility: hidden;
  transition: all 2s ease;
}
#winLoseDiv.show {
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  visibility: visible; 
}
#winLoseMessage {
  font-size: 5vw;
}
#scoreMessage {
  font-size: 4vw;
}
#playAgain {
  display: block;
}
#mainContainer {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center; 
  width: 100%;
  height: 100%;
  background-color: blue;
}

#wordleBox {
  width: 20em;
  height: 100%;
  background-color: lightgray;
  border: 3px solid lightgreen;
  border-radius: 2%;
  margin-bottom: 2em;
margin-top: 2em;
}
.helps {
  text-align: left;
  margin: 0;
  margin-left: 3px;
}
#hints {
  text-align: left;
  margin: 0;
  margin-left: 3px;
}
#error {
  color: red;
  font-weight: bold;
}

#playBtn {
  display: block;
  margin: auto;
  margin-top: 7px;
  margin-bottom: 5px;
  width: 5em;
  height: 3em;
  font-weight: bold;
  font-size: 20px;
}
#timer {
  margin-top: 2px;
  margin-bottom: 2px;
  color: red;
}
#title {
  margin-bottom: 1px;
}
#instructions {
  margin: 3px;
}
#lettersContainer {
  display: flex;
  text-align: center;
  justify-content: center; 
  width: 100%;
  height: 16.1em;
  margin-bottom: 1em;
  flex-direction: row;
  flex-wrap: wrap;
  
}
.letters {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 25px;
  width: 18%;
  height: 23%;
  margin-left: 2px;
  margin-right: 2px;
  margin-top: 5px;
  background-color: white;
  border: 1px solid white;
  border-radius: 5%;
}

#keyPad {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 99%;
  height: 8.5em;
  margin-top: 10px;
  margin-left: 2px;
}

.keys {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 8%;
  height: 30%;
  background-color: white;
  color: black;
  border-radius: 10%;
  margin-left: 3px;
  margin-right: 3px;
  margin-top: 2px;
  margin-bottom: 2px;
  border: none;
 
}
.keys:hover { 
  cursor: pointer;
  background-color: gray;
}
.keys:active { 
background-color: gray;
  cursor: pointer;
}
#a {
  margin-left: 20px;
}
#enter {
  font-size: 14px;
  width: 3em;
}
#delete {
  width: 2.5em;
}
#redX {
  color: red;
}
#helpsContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}
.helpsContainers {
   display: flex;
  color: white;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  width: 32%;
  height: 50px;
  margin-right: 2px;
  background-color: green;
}

.helpsContainers:hover {
  cursor: pointer;
  color: blue;
}
#playAgain {
  height: 3em;
  font-weight: bold;
  font-size: 20px;
  color: white;
  background-color: #12b84f;
  border: 2px solid white;
  border-radius: 5%;
}
#playAgain:hover {
  cursor: pointer;
  color: black;
  background-color: yellow;
  border-color: black;
}

@media (max-width: 800px) {
  #wordleBox {
    min-height: 10vh;
    width: 100vw;
    height: auto;
  }
  #title {
    margin: 0;
    margin-top: 2px;
  }
  #instructions {
    margin: 2px;
    margin-bottom: 1px;
  }
  #wordInput {
    margin: auto;
    margin-top: 1px;
    display: block;
    
  }
  .helpsContainers {
    height: 30px;
    font-size: 14px;
    margin-bottom: 3px;
  }
  
  #lettersContainer {
    height: 14em;
  }
}